import React, { useState, useEffect } from 'react';
import axios from "axios";
import {Link} from "react-router-dom";
import Carousel from 'react-bootstrap/Carousel'
function TopicDetail({ match }) {

    const [topic, SetTopic] = useState({})
    const [posts, SetPosts] = useState([])
    const [images, SetImage] = useState([])
    const id = match.params.id
    const pk = match.params.pk
    useEffect(() => {
        axios({
            method: "GET",
            url: `http://127.0.0.1:8000/api/board/${id}/topic/${pk}/`
        }).then(response => {
            SetTopic(response.data)
            SetPosts(response.data.posts)
            SetImage(response.data.photos)
        })
    }, [id])
    return(


<div className="App bordered mt-3 ">
    <div className="d-flex justify-content-center" style={{paddingLeft: '250px',paddingRight: '250px'}}>
        <div className="row">
            <div className="col-sm">
 <Carousel>
   {images.map(i => (
  <Carousel.Item>
    <img

      className="d-block"
      src={i.file}
      alt="First slide"
    />
  </Carousel.Item>

       ))}
</Carousel>
</div>
  </div>
</div>


           <table className="table table-bordered m-3 pl-3">
               <thead>
               <tr>
                   <th scope="col">User</th>
                   <th scope="col">Post</th>
                   <th scope="col">Time</th>
               </tr>
               </thead>
               {posts.map(p => (
               <tbody key="{p.id}">
               <tr>
                   <th >{p.created_by}</th>
                   <td>{p.message}</td>
                   <td>{p.created_at}</td>
               </tr>
               </tbody>
                   ))}
           </table>
</div>
    )
}
export default TopicDetail;